<template>
  <div id="supplier-component">
    <!--<div class="buttons-container">-->
    <!--<el-button type="success">My Homepage</el-button>-->
    <!--</div>-->
    <el-row class="row" :gutter="30">
      <el-col v-for="item in list" :key="item.id" class="col" :span="8">
        <SupplierCard :data="item"></SupplierCard>
      </el-col>
    </el-row>
  </div>
</template>

<script lang="ts">
import { defineComponent, computed, onMounted } from "vue";
import { useStore } from "vuex";
import SupplierCard from "@/components/SupplierCard.vue";
import { GetCompanies } from "@/typings/store";

const Component = defineComponent({
  name: "Supplier",
  components: {
    SupplierCard
  },
  setup() {
    const store = useStore();
    const list = computed(() => store.state.company.list);

    onMounted(async () => {
      await store.dispatch(GetCompanies, { trial_type: "supplier" });
    });

    return {
      list
    };
  }
});

export default Component;
</script>

<style scoped lang="stylus">
#supplier-component
  page-container()

  .buttons-container
    flex(flex-end)
    padding-bottom 30px

  :deep() .el-col
    margin-bottom 30px
</style>
