<template>
  <div id="supplier-card-component" @click="onCardClick">
    <div
      class="cover"
      :style="{ backgroundImage: `url('${formatImageUrl(data.logo.file)}')` }"
    ></div>
    <div class="content-container">
      <div class="title">{{ data.name }}</div>
      <div class="subtitle">{{ data["trial_type"] }}</div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, toRefs, PropType } from "vue";
import { useRouter } from "vue-router";
import { CompanyCardResponse } from "@/typings/company";
import { formatImageUrl } from "@/utils/image";

const Component = defineComponent({
  name: "SupplierCard",
  props: {
    data: {
      type: Object as PropType<CompanyCardResponse>,
      default: () => ({})
    }
  },
  setup(props) {
    const { data } = toRefs(props);
    const router = useRouter();

    const onCardClick = async () => {
      await router.push({
        name: "SupplierDetail",
        params: { id: data.value.id }
      });
    };

    return {
      onCardClick,
      formatImageUrl
    };
  }
});

export default Component;
</script>

